<template>
    <div class="issue-information">
        <el-form size="small" ref="addressForm"  :model="addressForm" class="form-box">
            <div class="item">
                <div class="tit">
                    {{ $L('标题') }}：
                </div>
                <el-form-item class="form-item">
                    <el-input type="text" size="small" v-model="addressForm.title" :placeholder="$L(' 最多可添加100个字符')"
                              maxlength="100"></el-input>
                </el-form-item>

            </div>
            <div class="item">
                <div class="tit time">
                    <span> {{ $L('显示时间') }}：</span>
                </div>
                <el-form-item class="form-item">
                    <el-date-picker
                            value-format="yyyy-MM-dd HH:mm:ss"
                            v-model="addressForm.dtCreate"
                            type="datetime"
                            :placeholder="$L('选择日期')"
                            @change="SearchTimeFunc">
                    </el-date-picker>
                    <p class="font-grey tips">{{ $L('显示给他人看的发布时间，默认为当前时间') }}</p>
                </el-form-item>
            </div>
            <div class="item item-two">
                <div class="tit">
                    {{ $L('描述') }}：
                </div>
                <el-form-item class="form-item">
                    <el-input v-model="addressForm.summary" type="textarea" maxlength="500" resize="none"
                              :placeholder="$L(' 最多可以添加500个字符，可留空！')"
                          rows="4"></el-input>
                </el-form-item>
                <el-form-item class="img-item">
                    <img v-if="addressForm.img === 'http://bbx11.nicebox.test' || addressForm.img === '' "
                         src="../../../assets/img/distributor/addpt.jpg" border="0" class="up-show" align="absmiddle">
                    <img v-else :src="addressForm.img" border="0" class="up-show" align="absmiddle">
                    <input id="fileinp" type="file" name="CommentsPic" class="img-input" :placeholder="$L('上传图片')"
                            accept="image/png,image/jpeg,image/jpg" @change="upImgFunc"/>
                </el-form-item>
            </div>
            <div class="item item-two">
                <div class="tit">
                    {{ $L('内容') }}：
                </div>
                <el-form-item class="form-item" style="text-align: center;">
                    <quill-editor class="editor" maxlength="50000"
                                  v-model="addressForm.content"
                                  :options="editorOption">
                    </quill-editor>
                </el-form-item>
            </div>
        </el-form>
        <div class="botton-btn">
            <el-button class="btn" type="primary" @click="saveInfoFun">{{ $L('发布信息') }}</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "issueInformation",
        data() {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                id: 0,
                megInfo: {},
                editorOption: {},
                uploadImg: [],
                addressForm: {
                    title: '',
                    img: '',
                    dtCreate: '',
                    summary: '',
                    content: "",
                },
                showImgUrl: '',
                addressRules: {
                    title: [
                        {required: true, message: '请输入正确的标题'}
                    ],
                }
            }
        },
        created() {
            this.addressForm.dtCreate =  this.formatDateTime(new Date());
            if (this.$route.params.hasOwnProperty('id')) {
                let id = this.$route.params.id;
                id = parseInt(id);
                if (isNaN(id)) {
                    id = 0
                }
                this.id = id;
            }
            if (this.id > 0) {
                this.getData();
            } else {
                this.loading = false;
            }
        },
        methods: {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                if (_self.id > 0) {
                    _self.loading = true;
                    _self.$api('readContentReleaseNews', {
                        id: _self.id
                    }, function (reData) {
                        if (reData.flag === 1) {
                            _self.megInfo = reData.data;
                            _self.addressForm.title = _self.megInfo.title;
                            _self.addressForm.dtCreate = _self.megInfo.dtCreate;
                            _self.addressForm.summary = _self.megInfo.summary;
                            _self.addressForm.content = _self.megInfo.content;
                            _self.addressForm.img = _self.megInfo.pic;
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'information'});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'information'});
                        });
                    });
                }
            },
            formatDateTime(date){
                let y = date.getFullYear();
                let m = date.getMonth() + 1;
                m = m < 10 ? ('0' + m) : m;
                let d = date.getDate();
                d = d < 10 ? ('0' + d) : d;
                let h = date.getHours();
                h=h < 10 ? ('0' + h) : h;
                let minute = date.getMinutes();
                minute = minute < 10 ? ('0' + minute) : minute;
                let second=date.getSeconds();
                second=second < 10 ? ('0' + second) : second;
                return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;
            },
            //上传图片
            upImgFunc(event) {
                const _self = this;
                let files = event.target.files[0];
                let imgUrl = null;
                let size = Math.floor(files.size / 1024);
                let type = files.type.substring(6);
                if (size > 2 * 1024 * 1024) {
                    _self.$Message.info('请选择2M以内的图片！');
                    return false;
                }
                if (!(type === 'png' || type === 'jpeg' || type === 'jpg')) {
                    _self.$Message.info('请选择png、jpeg、jpg的图片格式！');
                    return false;
                }
                _self.uploadImg = {
                    'file': files
                };
                let reader = new FileReader();
                reader.readAsDataURL(files);
                reader.onload = function (evt) {
                    _self.addressForm.img = evt.target.result;
                }
            },
            SearchTimeFunc(time) {
                console.log(time);
                this.addressForm.dtCreate = time;
            },
            backFunc() {
                this.$router.push({name:'information'});
            },
            saveInfoFun() {
                const _self = this;
                _self.loading = true;
                if (_self.addressForm.title !== '') {
                    _self.addressForm.img = _self.uploadImg;
                    let arr = {
                        id: _self.id,
                        title: _self.addressForm.title,
                        dtCreate: _self.addressForm.dtCreate,
                        summary: _self.addressForm.summary,
                        content: _self.addressForm.content,
                        smallImg: _self.addressForm.img?_self.addressForm.img:''
                    };
                    let postData = _self.$objToFormData(arr);
                    // console.log("title", postData.get("title"));
                    _self.$api('editReleaseNews', postData, function (reData) {
                        if (reData.flag === 1) {
                            _self.$message({
                                type: 'success',
                                message: _self.$L('发布成功!')
                            });
                            _self.$router.push({name:'information'});
                            _self.getData();
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'information'});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'information'});
                        });
                    }, true);
                } else {
                    _self.$message({
                        type: 'error',
                        message: _self.$L('标题不能为空!')
                    });
                    _self.loading = false;
                }

            },
        }
    }
</script>

<style lang="scss" scoped>
 .issue-information{
     position: absolute;
     width: 100%;
     top: 0;
     left: 0;
     background: #eff1f5;
     padding: 10px 0;
     margin-bottom: 5px;
     .form-box{
         margin-bottom: 50px;
         .item{
             padding: 10px 0;
             background: #fff;
             border-bottom: 1px solid #eff1f5;
             .form-item{
                 display: inline-block;
                 width: calc(100% - 100px);
                 margin: 0;
                 vertical-align: top;
                 .font-grey {
                     color: #999;
                     font-size: 12px;
                 }
                 .tips{
                     overflow: hidden;
                     white-space: nowrap;
                     text-overflow: ellipsis;
                 }
                 /deep/ .el-input{
                     width: 100%;
                 }
             }
             .tit{
                 width: 80px;
                 display: inline-block;
                 padding: 10px;
                 vertical-align: top;
             }
             .time{

             }
             &.item-two {
                 margin-top: 10px;
                 .tit{
                     display: block;
                 }
                 .form-item{
                     margin: 0 10px;
                     width: calc(100% - 20px);
                     .img-input {
                         width: 100px;
                         height: 100px;
                         position: absolute;
                         opacity: 0;
                         left: 0;
                         top: 0;
                         z-index: 9999;
                         cursor: pointer;
                     }
                     .up-show {
                         height: 100px;
                         width: 100px;
                         border: 1px solid #DBDBDB;
                     }
                     &.img-item{
                         margin: 10px 10px 0;
                         height: 100px;
                     }
                 }
                 .editor {
                     position: relative;
                     width: 100%;
                     height: 300px;
                     display: inline-block;
                 }
                 .img-item {
                     margin: 10px;
                     .img-input {
                         width: 100px;
                         height: 100px;
                         position: absolute;
                         opacity: 0;
                         left: 0;
                         top: 0;
                         z-index: 9999;
                         cursor: pointer;
                     }
                     .up-show {
                         height: 100px;
                         width: 100px;
                         border: 1px solid #DBDBDB;
                     }
                 }
             }
         }
     }
     .botton-btn{
         position: fixed;
         bottom: 0;
         width: 100%;
         background: #fff;
         z-index: 100;
         border-top: 1px solid #F2F6FC;
         text-align: center;
         padding: 8px 0;
         .btn{
             width: calc(100% - 20px);
         }
     }
 }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
    @media screen and (max-width:767px) {
        /deep/ .ql-formats {
            margin-right: 0 !important;
            float: left !important;
        }
    }
</style>